.slider{
    width: 100%;
    height: 350px;
    display: flex;
    gap:20px;
    z-index: 1000;

    .fullSlider{
        position: absolute;
        width:100vw;
        height:100vh;
        top:0;
        left:0;
        background-color: black;
        display: flex;
        justify-content: space-between;
        align-items: center;
        z-index: 1990;

        .arrow{
            flex:1;
            display: flex;
            align-items: center;
            justify-content: center;


            img{
                width:50px;

                &.right{
                    transform:  rotate((180deg));
                }
            }
        }

        .imageContainer{
            flex:10;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
              }
        }
        .close{
            position:absolute;
            top:0;
            right:0;
            color:white;
            font-size: 36px;
            font-weight: bold;
            padding: 50px;
            cursor: pointer;
        }
    }

    img{
        width:100%;
        height:100%;
        object-fit: cover;
        border-radius:10px ;
        cursor: pointer;
    }

    .bigImage{
        flex:3;
    }
    .smallImages{
        flex:1;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 20px;
        img{
            height: 100px;
        }
    }
}